import styles from "./Loading.module.scss";

// t componet ny trong 1 element c position relative => cn gia theo phn t.
// nu ch nh position prop = fiexd th cn gia mn hnh
import { forwardRef } from "react";
function Loading({ position }, ref) {
	return (
		<div
			style={{ position: position === "fixed" ? position : "absolute" }}
			className={styles["loading-wrapper"]}
			ref={ref}
		>
			<div className={styles["loading"]}></div>
		</div>
	);
}

export default forwardRef(Loading);
